import request from '@/common/js/request.js'
import tuiLoadmore from "@/components/loadmore/loadmore"
import tuiNomore from "@/components/nomore/nomore"

export default {
	components: {
		tuiLoadmore,
		tuiNomore
	},
	data() {
		return {
			pageIndex: 1,
			orderList: [],
			loadding: false,
			pullUpOn: true
		}
	},
	onLoad() {
		var param = {
		          'pageNum':this.pageIndex,
		        };
		request.get("/driver/order/list",param).
		then(res => {
			this.orderList = res.data;
			console.log("orderList",this.orderList)
		}).
		catch(e => console.log(e)) // 请求任务的结果处理
	},
	methods: {
		toMap() {
			this.$router.push('pages/map/map')
		},
		toCallPhone(tel) {
			 window.location.href = 'tel://'+tel
		},
		toOrder(orderId,userId){
			alert("下单成功")
		}
	},
	
	//页面相关事件处理函数--监听用户下拉动作
	onPullDownRefresh: function() {
		this.pageIndex = 1;
		var param = {'pageNum':pageIndex};	
				
		request.get("/driver/order/list",param).
		then(res => {
			if(res.data == undefined || res.data.length <= 0 ){
				console.log("no more",res.orderList);
			}else {
				this.orderList = res.data;
			}
			uni.stopPullDownRefresh();
			// this.tui.toast("刷新成功");
		}).
		catch(e => console.log(e)) // 请求任务的结果处理
	},

	// 页面上拉触底事件的处理函数
	onReachBottom: function() {
		this.pullUpOn = true; 
		if (this.loadding) return;
		this.loadding = true;
		
		var param = {'pageNum':this.pageIndex+1};
				
		request.get("/driver/order/list",param).
		then(res => {
			this.loadding = false;
			
			if(res.data == undefined || res.data.length <= 0 ){
				console.log("no more",res.orderList);
				this.pullUpOn = false; 
			}else {
				this.orderList = this.orderList.concat(res.data);
				console.log("orderList",this.orderList)
				this.pageIndex = this.pageIndex + 1;
			}
		}).
		catch(e => this.loadding = false) // 请求任务的结果处理

	}
}
